import {ContractContext} from './assets/abi/voting-contract';
import Web3 from "web3";
import WalletConnectProvider from "@walletconnect/web3-provider";
import copy from 'clipboard-copy';
import { formatEthFromWei, formatNumber } from './Formatters';

//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/voting-contract.json";
import TopAppBar from './components/AppBar';
import ConnectButton from './components/ConnectButton';

import taxMeterImg from './assets/img/tax-meter.png'
import {ReactComponent as TelegramIcon} from './assets/img/Telegram_logo.svg';
import SendIcon from '@mui/icons-material/Send';

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';



//const contractAddress: string = '0x6B4E65FB66F49655cBe8B4228319bB2c9E26aad8'; // release on mainnet
//const contractAddress: string = '0x5372579dc14033a8B6Ed8aB4120781B33bBB711b'; // release on mainnet 2022
const contractAddress: string = '0x4af53af7784f1a67f72fd623EB0Ab8C79aDA3700'; // release on mainnet 2023

//window.chainIdExpected = 97;
window.chainIdExpected = 56;


declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    web3: Web3;
    walletConnectProvider: WalletConnectProvider,
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[];
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
    chainId: number;
    chainIdExpected: number;
    bnbPrice: number;
  }
}

interface AppProps {
  style?: React.CSSProperties;
}


const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

window.dataRefreshInterval = 3000;

class App extends React.Component<AppProps> {
  
  state = {
    radioValue: "",
    progress: false,
    walletAccepted: false,
    alreadyVoted: false,
    totalYES: 0,
    totalNO: 0,
    totalVotes: 0,
    votingEnded: false
  }

  baseState: any;
  _stateInterval: any;

  constructor(props: AppProps) {
    super(props);

    this.baseState = this.state;
    // properties


  }


  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];


    this.checkState();

    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);

    //this.setModalOpen();
  }


  componentWillUnmount = () => { 
    
  }

  
  checkState = () => {  

    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }

    this.setState({
      yourAddress: window.walletAddress,
    });

    window.farm_contract.methods.getWalletStatus(window.walletAddress).call().then((result: boolean) => {
      //console.log(result);
      this.setState({
        walletAccepted: result
      });
    });


    window.farm_contract.methods.getUserAnswer(window.walletAddress).call().then((result: string) => {
      //console.log(result);
      this.setState({
        alreadyVoted: result != "0"
      });
    });

    window.farm_contract.methods.getTotalAnswers("1").call().then((resultYes: string) => {
      //console.log(result);
      let totalYES = parseInt(resultYes);


      window.farm_contract.methods.getTotalAnswers("2").call().then((resultNO: string) => {
        //console.log(result);
        let totalNO = parseInt(resultNO);

        this.setState({
          totalYES: (100 / (totalYES + totalNO) * totalYES),
          totalNO: (100 / (totalYES + totalNO) * totalNO),
          totalVotes: totalYES + totalNO
        });

        window.farm_contract.methods.started().call().then((started: boolean) => {
          //console.log(result);

          if(started == false && (totalYES + totalNO) > 0){
            this.setState({
              votingEnded: true
            });
          }


        });
        
      });
    });


  }

  onClick_Submit = () => {
    //alert(this.state.radioValue)
    let openValue = "";
    if(this.state.radioValue.toLowerCase() == "op1"){
      openValue = "1";
    }else if(this.state.radioValue.toLowerCase() == "op2"){
      openValue = "2";
    }

    this.setState({
      progress: true
    });

    window.farm_contract.methods.vote(openValue).send({from: window.walletAddress}).then((response) => {
      this.setState({
        progress: false
      });
    }).catch((err:any) => {
      alert("transaction failed");
      this.setState({
        progress: false
      });
    });
  }

  handleRadioChange = (event: any, _value: string) => {
    this.setState({
      radioValue: _value
    })
  }

  //  Main Layout
  //
  render = () => {
    return (
      <div>
      <div className={"wrapper"}>
        <img className={"logo"} src={"https://piston-token.com/images/logo.svg"}/>
        <h2>PISTON TOKEN</h2>
        <h3>final claim</h3>



      </div>
  <a id="details"></a>
      <div className={"wrapper left"} style={{marginTop: "16px"}}>
      <h4>Hello and welcome to the PSTN info and claim page.</h4>
As you all know, PSTN has unfortunately come to and end.<br/><br/> We have run a vote amongst the holders via the website and Telegram group and here are the results: <br/><br/>

81% of Voters decided that the best thing to do with the very little liquidity we had left ($45,000) was to invest it.<br/><br/> 
As promised, we will do exactly this via two FX trading bots and every $5000 collected in profit from our investment will be placed in a pool.<br/><b> If you are a voter, and have not gained back your initial investment in PSTN, you have been white listed for this pool.</b><br/><br/>

A claim button will appear from which you will be able to withdraw $50 per day per wallet. Users will be able to do this until the claim pool reaches zero. We will continue to fill the pool ideally monthly in increments of $5000 for the foreseeable future. 

We will be able to track all users and the amount each person is entitled to. Once we are down to the last $45,000 to give back, we will release the full $45,000 in one transaction thus bringing all users to break even giving a clear and final end to the project.

The next steps are to pull the liquidity and start the investment process. This will sell all tokens and bring the chart to $0. It is the only way to pull out the money remaining in this project. Once this is done, the steps explained above will start.
<br/><br/>
Thank you for being a part of the Piston Project
      </div>

      <div className={"wrapper left"} style={{marginTop: "16px"}}>
      <h4>Roadmap</h4>
       
          <p>‚úÖ pause trading</p>
          <p>‚úÖ voting</p>
          <p>‚úÖ setup tradingbots</p>
          <p>‚û°Ô∏è pull out the remaining liquidity</p>
          <p>üìÜ start trading</p>
          <p>üìÜ publish claim dapp</p>
      
      </div>

      </div>
    );
  }
}

export default App;
